body{
    background: black;
}
.title{
    color: white;
    text-align: center;
}
.todoContainer{
    max-width: 400px;
    margin: auto;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.todoCreateForm{
    display: flex;
    gap: 20px;
}
.todoInput{
    flex-grow: 1;
}
.buttonCreate{
    background: rgb(76, 0, 252);
    border: none;
    color: white;
    padding: 20px;
    cursor: pointer;
    border-radius: 10px;
    font-size: 16px;
    font-weight: bold;
}
.todosContainer{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.tarea{
    border: 1px solid white;
    background: white;
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    padding: 20px 20px;
    border-radius: 10px;
    align-items: center;
}
.buttonDelete{
    background: red;
    border: none;
    color: white;
    padding: 20px;
    cursor: pointer;
    border-radius: 10px;
    font-size: 16px;
    font-weight: bold;
}
.buttonEdit{
    background: green;
    border: none;
    color: white;
    padding: 20px;
    cursor: pointer;
    border-radius: 10px;
    font-size: 16px;
    font-weight: bold;

}
.buttonUpdate{
    background: red;
    border: none;
    color: white;
    padding: 20px;
    cursor: pointer;
    border-radius: 10px;
    font-size: 16px;
    font-weight: bold;
}
.buttons{
    display: flex;
    gap: 20px;
}
.formEdit{
    width: 100%;
    display: flex;
    justify-content: space-between;
    gap: 20px;
}
.inputTodoEdit{
    flex-grow: 1;
}